/*----------------------------------------
    Login Page
------------------------------------------*/
.login-bg {
  background:#f5f5f5;
  background-repeat: no-repeat;
  background-size: cover;
}

#login-page {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  height: 85vh;
}

#login-page .card-panel.border-radius-6.login-card {
  margin-left: 0 !important;    padding: 30px 20px;
}

#login-page .card-panel.border-radius-6.login-card h5{ border-bottom:solid 2px #ccc;padding-bottom: 15px; font-size: 30px;}

active-square .sidenav li:not(.open)>a.active>i, .sidenav-active-square .sidenav li>a.active>i{ color:#748ad0 !important;font-size: 35px;}
.sidenav li a.collapsible-header>i.material-icons, .sidenav li>a>i.material-icons{font-size: 35px;}
.nav-expanded .sidenav .menu-spacing{line-height: 20px;}
.menu-spacing span{position: relative; top: 13px;}

.menu-bottom{ background:#fff; padding-left:10px; font-size:17px; color:#000; font-weight:500;height: 50px;padding-top: 10px;}
.menu-bottom .right{ text-align:right}
.dashboard-content h1{ font-size:27px; text-align:center; padding-top:30px}

.padding-top{ padding-top:50px}

.dashboard-content .tiles{ background:#fff;text-align: center; padding: 30px 0;margin-bottom: 30px;}
.dashboard-content .tiles .material-icons{ color:#748ad0; font-size:70px; text-align:center; margin-bottom:20px; display: contents;}
.dashboard-content .tiles a{ text-align:center; font-size:18px; color:#000000; display:block;font-weight: 500;}

.password-settings{ margin:0 auto; width:50%; margin-top: 40px;}
/*----------------------------------------
  Modern Dashboard
------------------------------------------*/
.chartist-tooltip {
  z-index: 10000 !important;
}

.total-transaction-tooltip,
.current-balance-tooltip,
.user-statistics-tooltip,
.conversion-ratio-tooltip {
  background: #7b1fa2;
  color: #fff;
}

.total-transaction-tooltip:before,
.current-balance-tooltip:before,
.user-statistics-tooltip:before,
.conversion-ratio-tooltip:before {
  border-top-color: #7b1fa2;
}

.card .card-content .card-title i.material-icons {
  cursor: pointer;
}

.current-balance-container {
  position: relative;
  height: 170px;
}

.current-balance-container > * {
  position: absolute;
  width: 100%;
}

#current-balance-donut-chart {
  height: 170px;
  -webkit-filter: drop-shadow(0px 10px 4px rgba(133, 3, 168, 0.2));
  filter: drop-shadow(0px 10px 4px rgba(133, 3, 168, 0.2));
}

#current-balance-donut-chart .ct-series-a .ct-slice-donut {
  stroke: #ff4bac;
}

#current-balance-donut-chart .ct-series-b .ct-slice-donut {
  stroke: #f6f6f6;
}

.total-transaction-container {
  position: relative;
  height: 210px;
}

.total-transaction-container > * {
  position: absolute;
  width: 100%;
}

#total-transaction-line-chart {
  height: 240px;
}

#total-transaction-line-chart .ct-series-a.ct-series .ct-line {
  stroke: url(#lineLinearStats) !important;
  stroke-width: 3px;
}

#total-transaction-line-chart .ct-series-a .ct-point {
  fill: transparent;
  stroke: transparent;
}

#total-transaction-line-chart .ct-series-a .ct-point:hover {
  fill: url(#lineLinearStats) !important;
  stroke-width: 4;
  stroke: #fff;
}

#total-transaction-line-chart .ct-series-a .ct-point-circle {
  fill: #fff;
  stroke-width: 4;
  stroke: #7b1fa2;
}

#total-transaction-line-chart .ct-grid {
  stroke: rgba(0, 0, 0, 0.05);
  stroke-width: 1px;
  stroke-dasharray: 0px;
}

.total-transaction-shadow {
  -webkit-filter: drop-shadow(0px 30px 4px rgba(133, 3, 168, 0.25));
  filter: drop-shadow(0px 30px 4px rgba(133, 3, 168, 0.25));
}

.user-statistics-card .collection .collection-item {
  min-height: auto;
}

.user-statistics-container {
  position: relative;
  height: 150px;
}

.user-statistics-container > * {
  position: absolute;
  width: 100%;
}

#user-statistics-bar-chart {
  height: 150px;
  -webkit-filter: drop-shadow(4px 10px 4px rgba(133, 3, 168, 0.2));
  filter: drop-shadow(4px 10px 4px rgba(133, 3, 168, 0.2));
}

#user-statistics-bar-chart .ct-series-a .ct-bar {
  stroke: url(#barGradient1);
}

#user-statistics-bar-chart .ct-series-a .ct-slice-pie {
  fill: #ff4bac;
}

#user-statistics-bar-chart .ct-series-b .ct-bar {
  stroke: url(#barGradient2);
}

#user-statistics-bar-chart .ct-series-b .ct-slice-pie {
  fill: #8133ff;
}

.recent-buyers-card .collection {
  border: none;
}

.recent-buyers-card .collection .collection-item {
  min-height: auto;
  border-bottom: none;
